.poet-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    margin-top: 5px;
    margin-left: 2px;
    flex-wrap: nowrap;
    overflow-x: auto;
    gap:15px;
    scroll-behavior: smooth;
    scrollbar-width: none;
    padding: 10px;
  }




.p-card {
  flex: 0 0 auto;
}
  .p-card {
    width: 100px;
    height: 130px;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    border-radius: 15px;
    transition: transform 0.3s ease;
  }
  .p-card:hover {
    transform: scale(1.05);
  }
  .p-background-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
  }
  .p-gradient-layer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.484), transparent);
    z-index: 1;
  }
  .p-content {
    position: absolute;
    bottom: 0;
    /* left: 50%; */
    /* transform: translateX(-50%); */
    /* padding: 20px; */
    padding: 15px;
    padding-top: 500px;
    color: #fff;
    z-index: 2;
      display: flex;
  justify-content: space-between;
  align-items: center;
  }

  .p-content h5 {
    font-size: 0.8rem;
  }
  .p-content p {
    font-size: 0.5rem;
    font-weight: 300;
  }
  #p-play-icon {
      font-size: 0.8rem;
  }
  .p-content button
  {
     
    color: #ffffff;
  width: fit-content;
  padding: 7px 10px;
  flex: 1;
  border-radius: 100px;
  background: linear-gradient(50deg, rgba(146,25,133,1) 0%, rgba(25,59,146,1) 100%);    border: 0px solid transparent;
      transition: background-color 0.3s ease;
  }
  .p-content button:hover
  {
    background: white;
    color: blueviolet;
  }
