    /* display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 10px;
    margin-top: 20px; */
    padding: 10px;
    display: flex;
flex-wrap: wrap;
gap: 15px;
align-content: center;
justify-content: center;
  }
